<template>
  <section id="content">
    <base-jumbotron
      :src="require('@/assets/post-bg.jpg')"
      dark
    >
      <v-row
        align="center"
        justify="center"
      />
    </base-jumbotron>
    <base-card>
      <v-container class="py-0">
        <post :value="post" />
      </v-container>
    </base-card>
  </section>
</template>

<script>
  // Utilities
  import {
    mapState,
  } from 'vuex'

  export default {
    name: 'PostContents',

    components: {
      Post: () => import('@/components/Post'),
    },

    computed: {
      ...mapState('content', ['posts']),
      post () {
        return this.posts.find(p => p.slug === this.$route.params.slug)
      },
    },
  }
</script>
